
import React from "react";
import TripTable from "./triptable/TripTable";
import tripService from "../../../services/trips";


export default function AddTrip() {

    
    const [desc, setDesc] = React.useState({
        date:'',
        name:''
    });
       
    const [trips, setTrips] = React.useState([]);

    const getTrips = () => {
      tripService
      .getAll()
      .then(data => {
          setTrips(data);
          console.log('haetaan getAll()')
          console.log(data);}
          )
      .catch(err => console.error('Virhe: ' +err))
  }

  React.useEffect(() => {
      getTrips();
  }, [])

    const postTrip = (event) => {
    event.preventDefault();
    tripService
    .create(desc)
    .then(data => console.log(data))
    .catch(err => console.error(err));
    setTrips([...trips, desc]);
}
  
return(
<div>
    <p>lisää matka</p>
<form onSubmit={(event) => postTrip(event)}>
    <input value={desc.name} onChange={e => setDesc({...desc, name: e.target.value})} />
    <input value={desc.date} onChange={e => setDesc({...desc, date: e.target.value})} />
    <input type="submit" value="lisää matka"/>
    </form>

    <TripTable trips={trips}/>
</div>
)
}

